import React from "react"; 
import web from "../src/coffee.jpg";
import { NavLink, Route} from "react-router-dom";
import ScriptTag from 'react-script-tag';
import Re from "../src/Re";
import {Helmet} from "react-helmet";
import InternalCards from './Courses/InternalCards';
import Webdevelopment from './Courses/Webdevelopment';


const Cards = (props) => {


  return (
<React.Fragment>
       <Helmet>
              <script src="Re.js" type="text/javascript" />
            </Helmet>
            <ScriptTag type="text/javascript" src="Re.js" />
                  <div className='col-md-4 col-sm-4 mx-auto'>
                               <div className="card" >
                                <img className="card-img-top" src={props.imgsrc}  alt={props.imgsrc}/>
                                       <div className="card-body">


                                          <a id="title" href={props.link}
                                          className="card-title font-weight-bold" >{props.title}</a>

                                           <p className="card-text">{props.text}</p> 


                                         <a href={props.button} target = "_blank" rel= "noopener noreferrer" className="btn btn-primary"> Get Full Course </a>

                                       </div>
                                </div>

                  </div>
                 

</React.Fragment>



                  
    );
};

export default Cards;
