import React from "react";
import { NavLink, Route } from "react-router-dom";
import Webdevelopment from './Courses/Webdevelopment';
import InternalCards from "./Courses/InternalCards";


const Navbar = (props) => {
	return (
		<div>


          <div className="container-fluid nav_bg">
            <div className="row">
              <div className="col-10 mx-auto">

            


			<nav className="navbar navbar-expand-lg navbar-light bg-light">
  <NavLink className="navbar-brand" to="/" >Code with Hash</NavLink>
  <button className="navbar-toggler" type="button" data-toggle="collapse" data-target="#navbarSupportedContent" aria-controls="navbarSupportedContent" aria-expanded="false" aria-label="Toggle navigation">
    <span className="navbar-toggler-icon"></span>
  </button>

  <div className="collapse navbar-collapse" id="navbarSupportedContent">
    <ul className="navbar-nav ml-auto">

    
      <li className="nav-item active">
        <NavLink className="nav-link" exact activeStyle={{color:'blue'}}to="/">Home <span className="sr-only">(current)</span></NavLink>
      </li>


      <li className="nav-item">
        <NavLink className="nav-link" exact activeStyle={{color:'blue'}} to="/Service">Services</NavLink>
      </li>


       <li className="nav-item">
        <NavLink className="nav-link" exact activeStyle={{color:'blue'}} to="/About">About</NavLink>
      </li>



       <li className="nav-item">
        <NavLink className="nav-link" exact activeStyle={{color:'blue'}} to="/Contact">Contact</NavLink>
      </li>

      <Route
      exact path="/Webdevelopment" component={props.link}

      className="nav-item">
        <NavLink className="nav-link" exact activeStyle={{color:'blue'}} to="/Webdevelopment"></NavLink>
      

      </Route>


      
    </ul>
    
  </div>
</nav>
  </div>
            </div>
          </div>

		</div>
		)


}

export default Navbar;