import React from "react";
import web from "./about.png";
import { NavLink } from "react-router-dom";
import Common from "./Common";

const About = () => {

	const mystyle = {
      position : "relative",
      bottom : "0px"
    };
	return (
		<div>
        <Common name="Welcome to About Page" style={mystyle} imgsrc={web} visit='Contact' 
         btname="Contact Now"/>

         <img className="about" src="src/about.png"/>
		</div>


		);
};

export default About;